<template>
    <h1>Задачи</h1>
    <form v-on:submit="addTask">
        <input v-model="newTaskText" placeholder="Введите название задачи">
        <button>Добавить задачу</button>
    </form>
    <ul v-for="items in taskList" :key="items">
        <TaskItem :item="items" @deliteTask="deliteTask" @updateTask="updateTask"></TaskItem>
    </ul>
</template>

<script>
import TaskItem from './TaskItem.vue'
export default {
    name: 'TaskPage',
    components: {
        TaskItem
    },
    data() {
        return {
            taskList: [],
            newTaskText: ''
        }
    },
    created() {
        this.taskList = this.$store.state.list
    },
    methods: {
        addTask(e) {
            e.preventDefault()
            if(this.taskList.length!=0){
                this.taskList.push({ id: this.taskList[this.taskList.length-1].id+1, text: this.newTaskText })

            }
            else{
                this.taskList.push({ id: 0, text: this.newTaskText })
            }
            this.newTaskText=''
            this.$store.dispatch('editList', this.taskList)
        },
        filterList(index){
            return this.taskList.filter(function(itemm){
                return itemm.id !== index;
            });
        },
        deliteTask(index){
            this.taskList=this.filterList(index);
            this.$store.dispatch('editList', this.taskList)
        },
        findIndex(index){
            return this.taskList.findIndex(function (test) {
                return test.id == index
            })
            
        },
        updateTask(index,text){
            this.taskList[this.findIndex(index)].text = text
            this.$store.dispatch('editList', this.taskList)
        }
    }
}

</script>