<template>
    <input v-on:input="editText" :value="item.text">
    <button v-on:click="saveTask">Сохранить</button>
    <button v-on:click="deliteTask">Удалить</button>
</template>

<script>

export default {
    name: 'TaskItem',
    props: ['item'],
    emits:['updateTask','deliteTask'],
    data() {
        return {
            text: this.item.text,
        }
    },
    methods: {
        editText(e) {
            this.text = e.target.value
        },
        saveTask() {
            this.$emit('updateTask', this.item.id,this.text)
        },
        deliteTask() {
            this.$emit('deliteTask', this.item.id)
        }
    }
}

</script>